import React, { useState, useEffect } from 'react';
import IconScroll from "./IconScroll";

export default function Welcome(props) {

    const data = props.data;


    const gotoPage = (pageId) => {
        var elmnt = document.getElementById(pageId);
        elmnt.scrollIntoView({ behavior: 'smooth' });
    }
    return (
        <div id="welcomeP">
            <div className="cont">
                <main className="content">
                    <div className="left">
                        <div dangerouslySetInnerHTML={{ __html: data['welcome-message-1'] }} >
                        </div>
                        <div className="divider"></div>
                        <div dangerouslySetInnerHTML={{ __html: data['welcome-message-2'] }} >
                        </div>
                        <div>
                            <button className="yob" onClick={gotoPage.bind(this, "contact-page")}>
                                {data['contact-me']}
                            </button>
                        </div>
                    </div>
                    <div className="right">
                        <img className="portrait" id="playme" src={"https://afaithraf.github.io/my-portfolio/assets/me.png"} alt="ashraf portrait" />
                    </div>
                </main>
                <div className="bot">
                    <div style={{ width: 100 }}></div>
                    <div className="scroll">
                        <IconScroll color="#fff" iconColor="#f7f749" text="Start scrolling to see more" />
                    </div>
                    <div className="language">
                        <span onClick={() => props.setLang(false)}>èªž Change Language </span>
                    </div>
                </div>
            </div>
            <style jsx>{`



@keyframes rotateImg {
    100% {transform:rotate(360deg)}
}
#playme{
    animation-name: rotateImg;
    animation-duration: calc(var(--animation-duration) * 5.3);
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

// /////////////
                button.yob{
                    background: #ffff00;
                    color: #010101;
                    border: 2px solid #ffff00;
                    padding: 14px 60px;
                    font-size: 13pt;
                    font-weight: 600;
                    transition-duration: 200ms;
                    cursor: pointer;
                    margin-right: 20px;
                }
                button.yob.sm{
                    padding: 5px 20px;
                    font-weight: 500;
                    font-size: 13pt;
                }
                button.yob.reverse{
                    background: none;
                    color: #ffff00;
                    border: 2px solid #ffff00;
                }
                button.yob:hover{
                    background: #f7f749;
                    border-color: #f7f749;
                }
                .bold{
                    font-weight: bold;
                }
                .cont{
                    background: #111111;
                    height: 100vh;
                    width: 100%;
                    color: #f1f1f1;
                    display:flex;
                    flex-direction: column;
                    font-weight: 300;
                    position: fixed;
                    z-index: -1;
                    top: 0;
                }
                .bot{
                    margin: 0 auto;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    width: 90%;
                }
                .scroll{
                    margin-bottom: 20px;
                }
                .bot .language{
                    display: flex;
                    align-items: center;
                    cursor: pointer;
                    font-weight: bold;
                    color: #ffff00;
                }
                .content{
                    width: 70%;
                    margin: auto;
                    display: grid;
                    grid-template-columns: auto auto;
                    grid-gap: 16px;
                }
                .left{
                    margin-right: 100px;
                    font-size: 26pt;
                    max-width: 60vw;
                    text-align: left;
                    margin-top: 40px;
                }
                .divider{
                    height: 4px;
                    width: 140px;
                    background: #ffff00;
                }
                .portrait{
                    height: auto;
                    width: 460px;
                    max-width: 100%;
                    border: 2px solid #ffff00;
                    border-radius: 100%;
                }

                @media (max-width: 600px){
                    .navbar{
                        display:none;
                    }
                    .right{
                        grid-row-start: 1;
                    }
                    .left{
                        font-size: 1.1em;
                        margin: auto;
                        text-align: center;
                    }
                    .portrait{
                        width: 240px;
                    }
                    .content{
                        grid-template-columns: auto;
                        text-align: center;
                    }
                    .divider{
                        margin: auto;
                    }
                }

                @media (min-width: 600px){
                    .navbar{
                        display:none;
                    }
                    .left{
                        font-size: 1.3em;
                        margin: auto;
                        text-align: center;
                    }
                    .portrait{
                        width: 300px;
                    }
                    .right{
                        grid-row-start: 1;
                    }
                    .content{
                        grid-template-columns: auto;
                        text-align: center;
                    }
                    .divider{
                        margin: auto;
                    }
                }
                @media (min-width: 768px){
                    .navbar{
                        display:block;
                    }
                    .left{
                        font-size: 1.4em;
                        text-align: left;
                    }  
                    .portrait{
                        width: 240px;
                    }
                    .content{
                        grid-template-columns: auto auto;
                        text-align: left;
                    }
                    .right{
                        grid-column-start: 2;
                    }
                    .divider{
                        margin: 0;
                        margin-right: auto;
                    }
                }
                     
                @media (min-width: 992px){
                    .left{
                        font-size: 1.8em;
                    }  
                    .portrait{
                        width: 300px;
                    }
                }
                @media (min-width: 1400px){
                    .left{
                        font-size: 2.6em;
                    }                                  
                    .portrait{
                        width: 400px;
                    }
                }
            `}</style>
        </div >
    )
}
